/* bar on bottom  */

/* Styling the bar itself */
#topbar {
	cursor: none;
    position:fixed;
    bottom: -60px;
    display: block;
    z-index: 9999;
    box-sizing: border-box;
    width: 100%;
    padding: 10px;
    background-color: blue;
    color: #fff;
    font-size: 15px;
    text-align: center;
    -webkit-box-shadow: 0 0px 0px 0px black;
       -moz-box-shadow: 0 0px 0px 0px black;
            box-shadow: 0 0px 0px 0px black;
    transition: bottom 1s;
}
/* Hiding the checkbox */
#toggleTop {
    display: none;
}
/* Styling the lables, to show and hide the bar */
#showTop {
    position: fixed;
    z-index: 10;
    bottom: 0;
    right: 30px;
    display: block;
    padding: 10px;
    background-color: #000000;
    color: #fff;
    cursor: none;
}
#hideTop {
    float: right;
    display: inline-block;
}
/* Displaying the bar when the checkbox is checked */
#toggleTop:checked + #topbar {
    transition: bottom 1s;
    bottom:0px;
}
